import { LocaleData } from '../locale/locale.class';
import { getHours } from '../utils/date-getters';
import { getDayOfWeek } from '../units/day-of-week';

//! moment.js locale configuration
//! locale : Ukrainian [uk]
//! author : zemlanin : https://github.com/zemlanin
//! Author : Menelion Elensle : https://github.com/Oire

function plural(word, num) {
  let forms = word.split('_');
  return num % 10 === 1 && num % 100 !== 11 ? forms[0] : (num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20) ? forms[1] : forms[2]);
}

function relativeTimeWithPlural(num: number, withoutSuffix, key): string {
  let format: { [key: string]: string } = {
    ss: withoutSuffix ? '__' : '__',
    mm: withoutSuffix ? '__' : '__',
    hh: withoutSuffix ? '__' : '__',
    dd: '__',
    MM: '__',
    yy: '__'
  };

  if (key === 'm') {
    return withoutSuffix ? '' : '';
  }

  if (key === 'h') {
    return withoutSuffix ? '' : '';
  }
  return num + ' ' + plural(format[key], +num);
}

function weekdaysCaseReplace(date: Date, format: string, isUTC?: boolean): string | string[] {
  let weekdays = {
    nominative: '______'.split('_'),
    accusative: '______'.split('_'),
    genitive: '______'.split('_')
  };

  if (!date) {
    return weekdays.nominative;
  }

  let nounCase = (/(\[[]\]) ?dddd/).test(format) ?
    'accusative' :
    ((/\[?(?:|)? ?\] ?dddd/).test(format) ?
      'genitive' :
      'nominative');
  return weekdays[nounCase][getDayOfWeek(date, isUTC)];
}

function processHoursFunction(str: string) {
  return function (date: Date): string {
    return str + '' + (getHours(date) === 11 ? '' : '') + '] LT';
  };
}

export const ukLocale: LocaleData = {
  abbr: 'uk',
  months: {
    format: '___________'.split('_'),
    standalone: '___________'.split('_')
  },
  monthsShort: '___________'.split('_'),
  weekdays: weekdaysCaseReplace,
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  longDateFormat: {
    LT: 'HH:mm',
    LTS: 'HH:mm:ss',
    L: 'DD.MM.YYYY',
    LL: 'D MMMM YYYY .',
    LLL: 'D MMMM YYYY ., HH:mm',
    LLLL: 'dddd, D MMMM YYYY ., HH:mm'
  },
  calendar: {
    sameDay: processHoursFunction('[ '),
    nextDay: processHoursFunction('[ '),
    lastDay: processHoursFunction('[ '),
    nextWeek: processHoursFunction('[] dddd ['),
    lastWeek(date: Date) {
      switch (getDayOfWeek(date)) {
        case 0:
        case 3:
        case 5:
        case 6:
          return processHoursFunction('[] dddd [')(date);
        case 1:
        case 2:
        case 4:
          return processHoursFunction('[] dddd [')(date);
      }
    },
    sameElse: 'L'
  },
  relativeTime: {
    future: ' %s',
    past: '%s ',
    s: ' ',
    ss: relativeTimeWithPlural,
    m: relativeTimeWithPlural,
    mm: relativeTimeWithPlural,
    h: '',
    hh: relativeTimeWithPlural,
    d: '',
    dd: relativeTimeWithPlural,
    M: '',
    MM: relativeTimeWithPlural,
    y: '',
    yy: relativeTimeWithPlural
  },
  // M. E.: those two are virtually unused but a user might want to implement them for his/her website for some reason
  meridiemParse: /|||/,
  isPM(input) {
    return /^(|)$/.test(input);
  },
  meridiem(hour, minute, isLower) {
    if (hour < 4) {
      return '';
    } else if (hour < 12) {
      return '';
    } else if (hour < 17) {
      return '';
    } else {
      return '';
    }
  },
  dayOfMonthOrdinalParse: /\d{1,2}-(|)/,
  ordinal(_num: number, period: string): string {
    const num = Number(_num);
    switch (period) {
      case 'M':
      case 'd':
      case 'DDD':
      case 'w':
      case 'W':
        return num + '-';
      case 'D':
        return num + '-';
      default:
        return num.toString();
    }
  },
  week: {
    dow: 1, // Monday is the first day of the week.
    doy: 7  // The week that contains Jan 1st is the first week of the year.
  }
};
