import { LocaleData } from '../locale/locale.class';
import { getWeek } from '../units/week';
import { getDayOfWeek } from '../units/day-of-week';

//! moment.js locale configuration
//! locale : Russian [ru]
//! author : Viktorminator : https://github.com/Viktorminator
//! Author : Menelion Elensle : https://github.com/Oire
//! author :   : https://github.com/socketpair

function plural(word: string, num: number): string {
  let forms = word.split('_');
  return num % 10 === 1 && num % 100 !== 11 ? forms[0] : (num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20) ? forms[1] : forms[2]);
}

function relativeTimeWithPlural(num: number, withoutSuffix: boolean, key: string): string {
  let format: {[key: string]: string} = {
    ss: withoutSuffix ? '__' : '__',
    mm: withoutSuffix ? '__' : '__',
    hh: '__',
    dd: '__',
    MM: '__',
    yy: '__'
  };
  if (key === 'm') {
    return withoutSuffix ? '' : '';
  }
  return num + ' ' + plural(format[key], +num);
}

let monthsParse = [/^/i, /^/i, /^/i, /^/i, /^[]/i, /^/i, /^/i, /^/i, /^/i, /^/i, /^/i, /^/i];

// http://new.gramota.ru/spravka/rules/139-prop :  103
//  : http://new.gramota.ru/spravka/buro/search-answer?s=242637
// CLDR data:          http://www.unicode.org/cldr/charts/28/summary/ru.html#1753
export const ruLocale: LocaleData = {
  abbr: 'ru',
  months: {
    format: '___________'.split('_'),
    standalone: '___________'.split('_')
  },
  monthsShort: {
    //  CLDR  "."  ".",        ?
    format: '._._._.____._._._._.'.split('_'),
    standalone: '._.__.____._._._._.'.split('_')
  },
  weekdays: {
    standalone: '______'.split('_'),
    format: '______'.split('_'),
    isFormat: /\[ ?[] ?(?:||)? ?\] ?dddd/
  },
  weekdaysShort: '______'.split('_'),
  weekdaysMin: '______'.split('_'),
  monthsParse,
  longMonthsParse: monthsParse,
  shortMonthsParse: monthsParse,

  //    ,   ,  ,  4 ,      
  monthsRegex: /^([]|\.?|[]|?\.?|?|\.?|[]|\.?|[]|[]|\.?|[]|\.?|?|\.?|[]|?\.?|[]|\.?|[]|?\.?|[]|\.?)/i,

  //  
  monthsShortRegex: /^([]|\.?|[]|?\.?|?|\.?|[]|\.?|[]|[]|\.?|[]|\.?|?|\.?|[]|?\.?|[]|\.?|[]|?\.?|[]|\.?)/i,

  //    
  monthsStrictRegex: /^([]|[]|?|[]|[]|[]|[]|?|[]|[]|[]|[])/i,

  // ,     
  monthsShortStrictRegex: /^(\.|?\.|[.]|\.|[]|[.]|[.]|\.|?\.|\.|?\.|\.)/i,
  longDateFormat: {
    LT: 'H:mm',
    LTS: 'H:mm:ss',
    L: 'DD.MM.YYYY',
    LL: 'D MMMM YYYY .',
    LLL: 'D MMMM YYYY ., H:mm',
    LLLL: 'dddd, D MMMM YYYY ., H:mm'
  },
  calendar: {
    sameDay: '[ ] LT',
    nextDay: '[ ] LT',
    lastDay: '[ ] LT',
    nextWeek(date: Date, now: Date) {
      if (getWeek(now) !== getWeek(date)) {
        switch (getDayOfWeek(date)) {
          case 0:
            return '[ ] dddd [] LT';
          case 1:
          case 2:
          case 4:
            return '[ ] dddd [] LT';
          case 3:
          case 5:
          case 6:
            return '[ ] dddd [] LT';
        }
      } else {
        if (getDayOfWeek(date) === 2) {
          return '[] dddd [] LT';
        } else {
          return '[] dddd [] LT';
        }
      }
    },
    lastWeek(date: Date, now: Date) {
      if (getWeek(now) !== getWeek(date)) {
        switch (getDayOfWeek(date)) {
          case 0:
            return '[ ] dddd [] LT';
          case 1:
          case 2:
          case 4:
            return '[ ] dddd [] LT';
          case 3:
          case 5:
          case 6:
            return '[ ] dddd [] LT';
        }
      } else {
        if (getDayOfWeek(date) === 2) {
          return '[] dddd [] LT';
        } else {
          return '[] dddd [] LT';
        }
      }
    },
    sameElse: 'L'
  },
  relativeTime: {
    future: ' %s',
    past: '%s ',
    s: ' ',
    ss: relativeTimeWithPlural,
    m: relativeTimeWithPlural,
    mm: relativeTimeWithPlural,
    h: '',
    hh: relativeTimeWithPlural,
    d: '',
    dd: relativeTimeWithPlural,
    M: '',
    MM: relativeTimeWithPlural,
    y: '',
    yy: relativeTimeWithPlural
  },
  meridiemParse: /|||/i,
  isPM(input) {
    return /^(|)$/.test(input);
  },
  meridiem(hour, minute, isLower) {
    if (hour < 4) {
      return '';
    } else if (hour < 12) {
      return '';
    } else if (hour < 17) {
      return '';
    } else {
      return '';
    }
  },
  dayOfMonthOrdinalParse: /\d{1,2}-(||)/,
  ordinal(_num: number, period: string): string {
    const num = Number(_num);
    switch (period) {
      case 'M':
      case 'd':
      case 'DDD':
        return num + '-';
      case 'D':
        return num + '-';
      case 'w':
      case 'W':
        return num + '-';
      default:
        return num.toString(10);
    }
  },
  week: {
    dow: 1, // Monday is the first day of the week.
    doy: 4  // The week that contains Jan 4th is the first week of the year.
  }
};
